.forecast-controll--settings {
  width: 30%;
  display: flex;
  @media only screen and (max-width: 530px) {
    width: 100%;
    justify-content: center;
    order: 1;
  }
  button {
    width: 40px;
    height: 40px;
    border: none;
    background: transparent;
    border-radius: 3px;
    color: white;
    font-family: inherit;
    margin-right: 10px;
    box-shadow: 1px 1px 2px 2px darkgrey;
    font-size: 19px;
    padding: 0;
    outline: 0;
    &:hover {
      background: lightsalmon;
      cursor: pointer;
      outline: none;
      box-shadow: 2px 2px 5px 3px rgba(154, 154, 154, 0.7);
    }
    .fa-angle-down {
      margin-left: 3px;
      vertical-align: middle;
    }
  }
  select {
    border: none;
    background: transparent;
    border-radius: 3px;
    color: white;
    font-family: inherit;
    margin-right: 10px;
    box-shadow: 1px 1px 2px 2px darkgrey;
    font-size: 19px;
    padding: 0;
    outline: 0;
    option {
      background: lightsalmon;
    }
    &:hover {
      outline: none;
      cursor: pointer;
    }
    &:active {
      outline: none;
    }
  }
  .forecast-controll--settings-btn-wrapper {
    display: flex;
    button {
      font-weight: bold;
      margin-right: 0;
    }
  }
  .activeDegreesBtn {
    background: lightsalmon;
    box-shadow: 2px 2px 5px 3px rgba(154, 154, 154, 0.7);
    outline: none;
  }

  .rotateIn {
    animation-name: rotateIn;
    animation-duration: 2.3s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
  }

  .rubberBand {
    animation-name: rubberBand;
    animation-duration: 1.3s;
  }

  .tada {
    animation-name: tada;
    animation-duration: 1.0s;
  }

  @keyframes rotateIn {
    from {
      transform-origin: center;
      transform: rotate3d(0, 0, 1, -200deg);
      opacity: 1;
      color: lightsalmon;
    }

    to {
      transform-origin: center;
      transform: translate3d(0, 0, 0);
      opacity: 1;
      color: lightsalmon;
    }
  }

  @keyframes rubberBand {
    from {
      transform: scale3d(1, 1, 1);
    }

    30% {
      transform: scale3d(1.25, 0.75, 1);
    }

    40% {
      transform: scale3d(0.75, 1.25, 1);
    }

    50% {
      transform: scale3d(1.15, 0.85, 1);
    }

    65% {
      transform: scale3d(0.95, 1.05, 1);
    }

    75% {
      transform: scale3d(1.05, 0.95, 1);
    }

    to {
      transform: scale3d(1, 1, 1);
    }
  }

  @keyframes tada {
    from {
      transform: scale3d(1, 1, 1);
    }

    10%,
    20% {
      transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
    }

    30%,
    50%,
    70%,
    90% {
      transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    }

    40%,
    60%,
    80% {
      transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    }

    to {
      transform: scale3d(1, 1, 1);
    }
  }
}
