.forecast-info {
  width: 100%;
  min-height: 500px;
  color: white;
  display: flex;
  justify-content: space-between;
  @media only screen and (max-width: 750px) {
    flex-direction: column;
    align-items: center;
  }
  .forecast-info--weather-info {
    width: 100%;
    max-width: 700px;
    color: white;
    margin-right: 15px;
    @media only screen and (max-width: 750px) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .forecast-info--current-location {
      font-size: 50px;
      @media only screen and (max-width: 750px) {
        width: 100%;
        text-align: center;
      }
      @media only screen and (max-width: 450px) {
        font-size: 30px;
      }
      .forecast-info--current-date {
        font-size: 24px;
        margin-top: 15px;
      }
    }
    .forecast-info--current-temperature-wrapper {
      display: flex;
      position: relative;
      margin-bottom: 50px;
      @media only screen and (max-width: 850px) {
        margin-top: 50px;
      }
      @media only screen and (max-width: 750px) {
        margin-top: 0;
        margin-bottom: 0px;
      }
      @media only screen and (max-width: 600px) {
        margin-top: 50px;
        margin-bottom: 50px;
      }
      @media only screen and (max-width: 450px) {
        width: 100%;
        flex-direction: column;
        align-items: center;
        margin: 0;
      }
      .forecast-info--current-temperature {
        font-size: 180px;
        display: flex;
        @media only screen and (max-width: 850px) {
          font-size: 120px;
        }
        @media only screen and (max-width: 750px) {
          font-size: 180px;
        }
        @media only screen and (max-width: 600px) {
          font-size: 120px;
        }
        @media only screen and (max-width: 450px) {
          font-size: 120px;
          justify-content: center;
          margin-bottom: 20px;
        }
        span {
          padding-top: 20px;
        }
        .forecast-info--current-temperature-img {
          img {
            vertical-align: top;
            &:hover {
              animation-name: tada;
              animation-duration: 1.0s;
            }
          }
        }
      }
      .forecast-info--temperature-info {
        font-size: 24px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media only screen and (max-width: 450px) {
          font-size: 18px;
          flex-direction: row;
          justify-content: space-between;
          flex-wrap: wrap;
        }
        span {
          margin-bottom: 10px;
          @media only screen and (max-width: 450px) {
            width: 50%;
            font-size: 14px;
          }
        }
      }
    }
    .forecast-info--forecast-wrapper {
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 640px;
      @media only screen and (max-width: 750px) {
        margin-bottom: 30px;
      }
      @media only screen and (max-width: 750px) {
        flex-direction: column;
        align-items: center;
      }
      .forecast-info--forecast {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        width: 33%;
        min-width: fit-content;
        @media only screen and (max-width: 450px) {
          width: 100%;
          margin-bottom: 15px;
          box-shadow: 1px 1px 5px 2px white;
        }
        p {
          font-size: 35px;
          @media only screen and (max-width: 1000px) {
            font-size: 25px;
          }
          @media only screen and (max-width: 750px) {
            font-size: 35px;
          }
          @media only screen and (max-width: 600px) {
            font-size: 25px;
          }
          @media only screen and (max-width: 600px) {
            width: 100%;
          }
          @media only screen and (max-width: 450px) {
            font-size: 40px;
          }
        }
        div {
          display: flex;
          .forecast-info--temp {
            font-size: 60px;
            padding-top: 25px;
            @media only screen and (max-width: 850px) {
              font-size: 40px;
            }
            @media only screen and (max-width: 750px) {
              font-size: 60px;
            }
            @media only screen and (max-width: 600px) {
              font-size: 40px;
            }
            @media only screen and (max-width: 450px) {
              font-size: 50px;
            }
          }
          .forecast-info--image img {
            width: 75px;
            height: 75px;
            &:hover {
              animation-name: tada;
              animation-duration: 1.0s;
            }
          }
        }
      }
    }
  }
  .forecast-info--map-location {
    width: 100%;
    max-width: 440px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    .mapboxgl-map {
      width: 100%;
      height: 100%;
      max-width: 430px;
      height: 360px;
      box-shadow: 0px 0px 3px 4px white;
      border-radius:10px;
      margin-bottom: 20px;
    }
    .forecast-info--coords {
      font-size: 26px;
      text-align: right;
    }
  }
}

@keyframes tada {
  from {
    transform: scale3d(1, 1, 1);
  }

  10%,
  20% {
    transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
  }

  30%,
  50%,
  70%,
  90% {
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
  }

  40%,
  60%,
  80% {
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}
