.forecast-controll--search-block {
  display: flex;
  justify-content: flex-end;
  width: 45%;
  position: relative;
  @media only screen and (max-width: 530px) {
    width: 100%;
    justify-content: center;
    order: 0;
    margin-bottom: 20px;
  }
    input {
      width: 240px;
      height: 40px;
      outline: none;
      border: none;
      padding: 5px;
      box-sizing: border-box;
      box-shadow: -1px 1px 2px 2px white;
      color: white;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      font-size: 19px;
      letter-spacing: 0.9px;
      background: transparent;
      &::-webkit-input-placeholder {
        opacity: 1;
        transition: opacity 0.9s ease;
        color: white;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 15px;
        letter-spacing: 0.9px;
      }
      &:focus::-webkit-input-placeholder {
        opacity: 0;
        transition: opacity 0.9s ease;
        color: rgba(0, 0, 0, 0.3);
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-size: 19px;
        letter-spacing: 0.9px;
      }
    }
    input[type='submit'] {
      width: 90px;
      height: 40px;
      outline: none;
      border: none;
      background: rgba(154, 154, 154, 0);
      box-shadow: -1px 1px 2px 2px white;
      font-family: inherit;
      letter-spacing: 1.2px;
      color: lightsalmon;
      &:hover,
      &:focus,
      &:active {
        cursor: pointer;
      }
    }
    .voiceSearch {
      width: 35px;
      height: 35px;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      right: 22%;
      top: 4%;
      border-radius: 50%;
      font-size: 20px;
      color: white;
      &:hover {
        cursor: pointer;
        color: lightsalmon;
      }
      @media only screen and (max-width: 1000px) {
        right: 24%;
        top: 4%;
      }
      @media only screen and (max-width: 865px) {
        right: 29%;
        top: 4%;
      }
      @media only screen and (max-width: 700px) {
        right: 38%;
        top: 4%;
      }
      @media only screen and (max-width: 530px) {
        right: 34%;
        top: 4%;
      }
    }
}

.heartBeat {
  animation-name: heartBeat;
  animation-duration: 2.3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@keyframes heartBeat {
  0% {
    transform: scale(1);
  }

  14% {
    color: lightsalmon;
    transform: scale(1.3);
  }

  28% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  42% {
    color: lightsalmon;
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
  }

  70% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.pulse {
  animation-name: pulse;
  animation-duration: 1.0s;
}

@keyframes pulse {
  from {
    color: white;
    transform: scale3d(1, 1, 1);
  }

  50% {
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}
