.container {
  width: 100%;
  max-width: 1400px;
  min-height: 100vh;
  padding: 5% 5% 0 5%;
  box-sizing: border-box;
  margin: 0 auto;
  background: mediumturquoise;
  .forecast-controll {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    @media only screen and (max-width: 530px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100px;
      margin-bottom: 40px;
    }
  }
}
