.error-block {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9998;
  background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5));
  .error-block--modal {
    position: fixed;
    top: 40%;
    left: 15%;
    width: 70%;
    max-width: 750px;
    min-height: 150px;
    z-index: 9999;
    background: white;
    border-radius: 10px;
    border: 4px solid lightsalmon;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 2% 2%;
    box-sizing: border-box;
    .error-block--message {
      width: 100%;
      max-width: 680px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      align-self: center;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      font-size: 20px;
    }
    .error-block--button {
      width: 70px;
      height: 40px;
      border: none;
      border-radius: 8px;
      background: lightsalmon;
      color: white;
      outline: 0;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      font-size: 20px;
      &:hover {
        cursor: pointer;
      }
    }
  }

  .zoomOut {
    animation-name: zoomOut;
    animation-duration: 1.0s;
  }

  @keyframes zoomOut {
    from {
      opacity: 1;
    }

    50% {
      opacity: 0;
      transform: scale3d(0.3, 0.3, 0.3);
    }

    to {
      opacity: 0;
    }
  }
}

